import style from './frame.module.css';

import frame1 from '../../images/frame1.jpg';
import frame2 from '../../images/frame2.jpg';
import frame3 from '../../images/frame3.jpg';
import frame4 from '../../images/frame4.jpg';
import frame5 from '../../images/frame5.jpg';
import frame6 from '../../images/frame6.jpg';
import frame7 from '../../images/frame7.jpg';
import frame8 from '../../images/frame8.jpg';
import frame9 from '../../images/frame9.jpg';

function Frame({ frameRef }) {
  return (
    <section className={style.wrapper} ref={frameRef}>
      <div className={style.textBox}>
        <h2 className={style.title}>
          //  
        </h2>
        <p className={style.text}>
           &nbsp;   &nbsp; &nbsp;.   
           &nbsp;&mdash;   ,  &laquo;&raquo;,
             &nbsp;..
        </p>
        <p className={style.text}>
               :
        </p>
        <ul className={style.list}>
          <li className={style.item}>
               &nbsp;2&nbsp; ,   &nbsp;;
          </li>
          <li className={style.item}>
                &nbsp;70&nbsp;;
          </li>
          <li className={style.item}>
            &nbsp;&nbsp;&mdash;    ;
          </li>
          <li className={style.item}>
              &nbsp; .
          </li>
        </ul>
        <p className={style.text}>
            &nbsp;.   &nbsp; &nbsp; 
          &nbsp; &nbsp;&mdash;    
          &nbsp; .
        </p>
        <p className={style.text}>
            .   &nbsp; 
           &nbsp;  &nbsp;.
        </p>
        <p className={style.text}>
           2&nbsp;  &nbsp;.
        </p>
        <p className={style.text}>
              &nbsp;     &nbsp;.
        </p>
      </div>
      <div className={style.galleryBox}>
        <img className={style.img} src={frame1} alt="    " />
        <img className={style.img} src={frame2} alt="    " />
        <img className={style.img} src={frame3} alt="    " />
        <img className={style.img} src={frame4} alt="    " />
        <img className={style.img} src={frame5} alt="    " />
        <img className={style.img} src={frame6} alt="    " />
        <img className={style.img} src={frame7} alt="    " />
        <img className={style.img} src={frame8} alt="    " />
        <img className={style.img} src={frame9} alt="    " />
      </div>
    </section>
  );
}

export default Frame;