import style from './about.module.css';

import aboutone from '../../images/about-one.jpg';
import abouttwo from '../../images/about-two.jpg';
import aboutthree from '../../images/about-three.jpg';

function About({ aboutRef }) {
  return (
    <section className={style.wrapper} ref={aboutRef}>
      <div className={style.textBox}>
        <h2 className={style.title}>
          // &nbsp;
        </h2>
        <p className={style.text}>
              &nbsp;   : 
          , , , .  :   &nbsp;. 
        </p>
        <p className={style.text}>
          &nbsp; &nbsp; &nbsp; :  ,  , 
          ,  &nbsp; .   &nbsp;&nbsp;. 
            (,  ,  &nbsp;..). 
        </p>
        <p className={style.text}>
                  , 
               .
        </p>
        <p className={style.text}>
            ,    &nbsp; .
        </p>
        <p className={style.text}>
          &nbsp;:
        </p>
        <ul className={style.list}>
          <li className={style.item}>
                ;
          </li>
          <li className={style.item}>
            : , / (/, &nbsp;);
          </li>
          <li className={style.item}>
             ;
          </li>
          <li className={style.item}>
                  .
          </li>
        </ul>
        <p className={style.text}>
             &nbsp;&nbsp;&mdash;  .
        </p>  
        <p className={style.text}>
           &nbsp;  .
        </p>
      </div>
      <div className={style.imgBox}>
        <img className={style.img} src={aboutthree} width={546} height={340} alt="   " />
        <img className={style.img} src={abouttwo} width={546} height={340} alt="   " />
        <img className={style.img} src={aboutone} width={546} height={340} alt="   " />
      </div>
    </section>
  );
}

export default About;