import { useRef } from 'react';
import style from './cover.module.css';

import cover from '../../images/cover.jpg';

function Cover({ clickTab, contactsRef }) {
  
  return (
    <section className={style.wrapper}>
      <div className={style.description}>
        <h2 className={style.sutitle}> &nbsp; </h2>
        <span className={style.button} onClick={() => clickTab(contactsRef)}> </span>
        <p className={style.text}>   &nbsp;  .</p>
        <h1 className={style.title}> </h1>
      </div>
      <img className={style.img} src={cover} width={1340} height={544} alt="    " />
    </section>
  );
}

export default Cover;